<template>
  <div>
    <div class="container" v-if="status === null">
      Loading...
    </div>
    <div class="container" v-if="status">
      <select
        name="selectedYeard"
        v-model="selectedYear"
      >
        <option
          :value="new Date().getFullYear()"
          :selected="'selected'"
        >
          {{new Date().getFullYear()}}
        </option>
        <option :value="new Date().getFullYear()-1">{{new Date().getFullYear()-1}}</option>
        <option :value="new Date().getFullYear()-2">{{new Date().getFullYear()-2}}</option>
      </select>

      <select v-model="selectedYear">
        <option
          v-for="index in 12"
          :key="index + 1"
          :value="index"
          :selected="index == new Date().getMonth()+1 ? 'selected': ''"
        >
          {{index}}
        </option>
      </select>

      <div class="rwd-table">
        <table>
          <tr class="title">
            <td class="date">交易日期</td>
            <td class="bank">交易方式</td>
            <td class="type">交易類別</td>
            <td class="amount">交易金額</td>
          </tr>
          <template
            v-if="items, item[7] !== '0'"
            v-for="(item, index) in items.reverse()"
          >
            <tr
              :key="index"
            >

              <td class="date">
                {{ item[0].split(' ')[0] }}
              </td>
              <td class="bank">
                {{ item[1] }}
              </td>
              <td class="type">
                {{ item[3] }}
              </td>
              <td class="amount">
                {{ item[4] }}
              </td>
            </tr>
          </template>
        </table>
      </div>
    </div>
  </div>
</template>

<script>
import axios from 'axios'

export default {
  name: 'HelloWorld',
  components: {
  },
  data () {
    return {
      status: null,
      items: []
    }
  },
  created: function () {
    axios.get('https://sheets.googleapis.com/v4/spreadsheets/1Axd3WeIglDpqvrlCcsj1vodsU4_JdLgrY1DqJ3XsIKA/values/Current(Reciept)', {
      params: {
        key: 'AIzaSyCo4aab5RX5zOH5MOnRUtGHEL63ftpkknQ'
      }
    })
      .then((response) => {
        this.items = response.data.values
        this.status = 'updated'
      })
  }
}
</script>

<style lang="scss">
.rwd-table{
  display: block;
  width: 100%;
  height: auto;
  overflow-y:auto;
  table{
    width: 100%;
    min-width: 400px;
  }
}
</style>



