import Vue from 'vue'
import Vuex from 'vuex'

import * as settings from '@/settings'
import actions from './actions'
import mutations from './mutations'
import getters from './getters'

Vue.use(Vuex)

function getStateFromSettings () {
  var state = {}
  for (var key in settings.STORE_STATE) {
    settings.STORE_STATE[key].forEach(function (el) {
      state[key + el] = null
    })
  }
  return state
}

export default new Vuex.Store({
  state: getStateFromSettings(),
  actions,
  mutations,
  getters
})



