import * as stateTypes from '@/settings/stateTypes'

export default {
  getItems: (state) => (appType) => {
    return state[`${appType}${stateTypes.ITEMS}`]
  },
  getPaginator: (state) => (appType) => {
    return state[`${appType}${stateTypes.PAGINATOR}`]
  },
  getItem: (state) => (appType) => {
    return state[`${appType}${stateTypes.ITEM}`]
  }
}



