<template>
  <header class="header">
    <div class="container-fluid header content">
      <Google :userData="userData"/>
      <div
        class="container"
        v-if="userData.status === true && userData.vaild === true"
      >

        <div class="nav links">
          <div class="block">
            <router-link :to="'/home'">Accounting</router-link>
            <router-link :to="'/analytic'">Analytic</router-link>
          </div>
        </div>

        <h1
          class="page title"
        >{{userData.name}}'s Tally Book</h1>
      </div>
    </div>
  </header>
</template>

<script>
import Google from '@/components/Google'

export default {
  props: ['userData'],
  components: {
    Google
  }
}
</script>

<style lang="scss">
@import '../assets/sass/mixins/index.scss';
@import '../assets/sass/project/index.scss';
header.header{
  .header.content{
    padding-top: 1.25em;
    h1.page.title{
      @include projectFont;
      display:block;
      font-size: 1.1em;
      font-weight: normal;
      color: default(color, cuatro);
      text-shadow: 0 0 .3px default(color, cuatro);
      text-align: left;
      letter-spacing: 0.03em;
      padding-bottom:.5em;
      border-bottom:1px solid fade-out(default(color, tres), .5);
      line-height: 0em;
    }
    .nav.links{
      .block{
        margin: 0 auto 2.5em auto;
        display: flex;
        a{
          font-family: 'Logo', sans-serif !important;
          display: inline-block;
          padding: .5em 1em;
          line-height: 1em;
          border-width: 1px 0 1px 1px;
          border-style: solid;
          border-color: default(color, dos);
          color: default(color, cuatro);
          &:first-child{
            border-radius: 3px 0 0 3px;
          }
          &:last-child{
            border-radius: 0 3px 3px 0;
            border-right-width: 1px;
          }
          &[class*="active"]{
            background: default(color, cuatro);
            color: white;
          }
        }
      }
    }
  }
}
</style>



