import * as mutationTypes from './mutationTypes'
import api from '@/api'
import utils from '@/utils'

export default {
  getItems ({ commit, state }, appType) {
    const appEndpoints = api.appEndpoints(appType)
    var paginated = appEndpoints.paginated
    var page = state.route.params.page
    var url = appEndpoints.items
    if (paginated && page) {
      url += '?page=' + page
    }
    // console.log(`url = ${url}`)
    return api.get(url)
      .then((data) => {
        var items = data
        if (paginated) {
          commit(mutationTypes.SET_PAGINATOR, { appType, paginator: data.paginator })
          items = data.results
        }
        commit(mutationTypes.SET_ITEMS, { appType, items })
      })
  },
  getItem ({ commit, state }, appType) {
    const appEndpoints = api.appEndpoints(appType)
    var id = state.route.params.id
    var url = utils.stringFormat.call(appEndpoints.item, id)
    // console.log(`url = ${url}`)
    return api.get(url)
      .then((item) => {
        // console.log(item)
        commit(mutationTypes.SET_ITEM, { appType, item })
      })
  }
}



