<template>
  <div>
    <h2 class="title" v-html="settingsList.title"></h2>
    <section
      class="form-group row no-gutters element-selection options quickStart"
      v-if="settingsList.digital"
    >
      <label
        class="col"
        v-for="(item, index) in settingsList.digital"
        :key="index"
      >
        <input
          type="radio"
          :name="nameType"
          :data-settings='JSONSTRING(item.settings)'
          :value="item.settings"
          :required="checkRequire(settingsList.require, index)"
          @click="quickStart($event)"
        >
        <span v-html="item.title"></span>
      </label>
    </section>
  </div>
</template>

<script>
import $ from 'jquery'

export default {
  props: [
    'settingsList',
    'nameType',
    'defaultSettings'
  ],
  methods: {
    goToLink (item) {
      return item.link ? window.open(item.link, item.open_in_new ? '_blank' : '_self') : false
    },
    checkRequire (checkVal, index) {
      return checkVal === true && index === 0
    },
    quickStart (event) {
      let settings = $(event.target).data('settings').replace(/[{}'" ]/g, '').split(',')
      var jsonArray = {
        BankName: '',
        TradeType: '',
        Store: '',
        Info: '',
        Amount: '',
        Level: ''
      }
      settings.forEach(element => {
        var val = element.split(':')
        jsonArray[val[0]] = val[1]
      })
      this.$set(this.defaultSettings, 'BankName', jsonArray.BankName)
      this.$set(this.defaultSettings, 'TradeType', jsonArray.TradeType)
      this.$set(this.defaultSettings, 'Store', jsonArray.Store)
      this.$set(this.defaultSettings, 'Info', jsonArray.Info)
      this.$set(this.defaultSettings, 'Amount', jsonArray.Amount)
      this.$set(this.defaultSettings, 'Level', jsonArray.Level)
      $('*[name="Amount"]').focus()
    },
    JSONSTRING (string) {
      return '"' + string + '"'
    }
  },
  mounted: function () {
    // const _this = this
    // $('.quickStart *[data-settings]').click(function () {
    // })
  }
}
</script>

<style lang="scss">
@import '../assets/sass/mixins/index.scss';

h2.title{
  position: relative;
  font-size:1.3em;
  text-indent:.3em;
  color:default(color, tres);
  letter-spacing: .05em;
  &:before{
    content:'';
    position: absolute;
    top: 50%;
    left: 0;
    display:inline-block;
    width: .1em;
    height: 1em;
    margin-top:-.6em;
    background:fade-out(default(color, tres),.5);
  }
}

.form-group{
  margin-bottom:2.5em;
  &.element-selection.options{
    border-bottom: 1px solid default(color, dos);
    &.level{
      border-right: 1px solid default(color, dos);
      label{
        flex: 1 0 20% !important;
        border-right:0;
      }
    }
    label{
      //grid
      @include rps(u){
        $divid: 6;
        flex: 1 0 calc(100% / #{$divid});
        &:nth-child(#{$divid}n){
          border-right-width: 1px;
        }
      }
      $index: lg md;
      $divid: 4 2;
      @each $now in $index{
        $i: index($index, $now);
        @include breakpoint-d(default(vp, #{$now})){
          $divider: #{nth($divid, $i)};
          flex: 1 0 calc(100% / #{$divider});
          &:nth-child(#{$divider}n){
            border-right-width: 1px;
          }
        }
      }

      background:transparent;
      text-align: center;
      border-color:default(color, dos);
      border-style: solid;
      border-top-width:1px;
      border-right-width:0px;
      border-left-width:1px;
      border-bottom-width: 0;
      padding: 0;margin:0;
      cursor: pointer;
      &:hover{
        input+span{
          background:fade-out(default(color, tres),.95);
        }
      }
      &:first-child{
        @media (min-width:768px){
          border-radius: default(manually, radius) 0 0 default(manually, radius);
        }
      }
      &:last-child{
        border-right-width:1px;
        @media (min-width768px){
          border-radius: 0 default(manually, radius) default(manually, radius) 0;
        }
      }
      input{
        visibility: hidden;
        display:none;
        +span{
          display: flex;
          width:100%;height:100%;
          align-items:center;
          justify-content: center;
          color: default(color, cuatro);
          padding:.35em 0;
          transition:.1s ease;
          background:rgba(255,255,255,1);
        }
        &:checked+span{
          background: default(color, cuatro);
          color:white;
        }
      }
      input[type="radio"][name="fastButton"]{
        &:checked+span{
          background: default(color, cinco);
        }
      }
    }
  }
  input[type="text"],input[type="number"]{
    padding:.4em .5em;
    border-radius:default(manually, radius);
    border-color:default(color, dos);
    border-radius: 0;
    @media (max-width:767px){
      border-radius:0;
    }
  }
  input[type="submit"]{
    cursor: pointer;
    padding:.4em .5em;
    display: block;
    width:100%;
    border-radius: default(manually, radius);
    background-color:default(color, cuatro);
    border:0;
    color:white;
    @media (max-width:767px){
      border-radius: 0;
    }
  }
}
</style>



