<template>
<div>
  <div class="hidden-xs-up">
    <iframe id="actionFrame" src="about:blank"></iframe>
  </div>

  <div class="modal fade" id="exampleModal" tabindex="-1" role="dialog" aria-labelledby="exampleModalLabel" aria-hidden="true">
    <div class="modal-dialog" role="document">
      <div class="modal-content">
        <div class="modal-header">
          <span class="modal-title hidden-xs-up" id="exampleModalLabel">New message</span>
          <button type="button" class="close hidden-xs-up" data-dismiss="modal" aria-label="Close">
            <span aria-hidden="true">&times;</span>
          </button>
        </div>
        <div class="modal-body">
          <span class="h5 text-center d-block pt-5 pb-5">Loading...</span>
        </div>
      </div>
    </div>
  </div>
</div>
</template>

<script>
import $ from 'jquery'

export default {
  props: [
    'defaultSettings'
  ],
  mounted: function () {
    const _this = this
    $('#actionFrame').on('load', function () {
      setTimeout(function () {
        $('#exampleModal').modal('hide')
      }, 500)
      if (document.getElementById('actionFrame').src !== 'about:blank') {
        $('#AlertContent').prepend(
          '<div class="alert alert-success fade show mt-3" role="alert"><button type="button" class="close" data-dismiss="alert" aria-label="Close" data-time="5"><span aria-hidden="true">&times;</span></button><h4 class="h4">Submited</h4><span> - Amount: <b>' + _this.defaultSettings.Amount + '</b></span></div>'
        )
        // clear settings
        $('#controller-form')[0].reset()
        _this.$parent.defaultSettings = {}

        document.getElementById('actionFrame').src = 'about:blank'
      }
    })
  }
}
</script>

<style lang="scss">
@import '../assets/sass/mixins/index.scss';
.hidden-xs-up{
  iframe{
    display: block;
    width: 0;
    height: 0;
    @include opacity(0)
  }
}
#AlertContent{
  b{
    color: rgb(80,80,80) !important;
    font-weight: bold;
  }
}
</style>



