<template>
  <div id="wrapper">
    <TheHeader :userData="userData"/>
    <main class="main">
      <router-view
        v-if="userData.status && userData.vaild === true"
      />
      <div
        class="container vailFaild"
        v-if="userData.status && userData.vaild === false"
      >
        <div class="google authorization">
          <div class="g-signin2">
            <div class="block">
              <div class="content">
                <div class="text">
                  <h1 class="title">Something went wrong</h1>
                </div>
                <p>
                  <span class="name">{{ userData.name }}</span>
                  <span class="email">({{ userData.email }})</span>
                </p>
                <p>
                  Seems like this account is not belong to Fryan, this function cannot work in this account.
                </p>
                <div class="google icon deco"></div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </main>
    <TheFooter />
  </div>
</template>

<script>
import TheHeader from '@/components/TheHeader'
import TheFooter from '@/components/TheFooter'

export default {
  data () {
    return {
      userData: {
        id: null,
        name: null,
        email: null,
        photo: null,
        status: null,
        vaild: false
      }
    }
  },
  components: {
    TheHeader,
    TheFooter
  }
}
</script>

<style lang="scss">
@import './assets/sass/main';

html, body , #wrapper{
  height: 100%;
  padding: 0;
  margin: 0;
}

#wrapper{
  width: 100%;
  height: 100%;
  display: table;
  table-layout:fixed;
  header.header, footer.footer, main.main{
    display: table-row;
    width: 100%;
  }
  header.header, footer,footer{
    height: 1px;
  }

  background:rgba(250,250,250,1);
  main.main{
    height: auto;
    .container{
      padding-top: 1.25em;
      padding-bottom: 1.25em;
    }
  }
}
.vailFaild{
  *{
    font-family: 'Loto', sans-serif !important;
  }
  span{
    &.name{
      font-weight: bold;
    }
    &.email{
      padding-left: .25em;
      font-size: .9em;
    }
  }
}
</style>



