<template>
  <div class="container-fluid pl-0 pr-0">
    <div class="container">

      <form id="controller-form" v-on:submit.prevent="submitMethod">
        <HomepageShortCut
          :settingsList="quickStartList"
          :nameType="'fastButton'"
          :defaultSettings="defaultSettings"
        />

        <BaseOption
          :settingsList="bankList"
          :nameType="'BankName'"
          :defaultSettings="defaultSettings"
        />

        <BaseOption
          :settingsList="tradeTypeList"
          :nameType="'TradeType'"
          :defaultSettings="defaultSettings"
        />

        <div class="row">
          <div class="col-lg-6">
            <h2 class="title">Store</h2>
            <div class="form-group row no-gutters">
              <input class="form-control" type="text" name="Store" v-model="defaultSettings.Store">
            </div>
          </div>

          <div class="col-lg-6">
          <h2 class="title">Items</h2>
          <div class="form-group row no-gutters">
            <input class="form-control" type="text" name="Info" v-model="defaultSettings.Info">
          </div>
          </div>

          <div class="col-lg-6">
          <h2 class="title">Amount</h2>
          <div class="form-group row no-gutters">
            <input class="form-control" type="number" name="Amount" required="required" min="0" pattern="\d*" v-model="defaultSettings.Amount">
          </div>
          </div>

          <div class="col-lg-6">
          <h2 class="title">Level</h2>
          <div class="form-group row no-gutters element-selection options level">
            <label class="col"><input type="radio" name="Level" value="1" v-model="defaultSettings.Level"><span>1</span></label>
            <label class="col"><input type="radio" name="Level" value="2" v-model="defaultSettings.Level"><span>2</span></label>
            <label class="col"><input type="radio" name="Level" value="3" v-model="defaultSettings.Level"><span>3</span></label>
            <label class="col"><input type="radio" name="Level" value="4" v-model="defaultSettings.Level"><span>4</span></label>
            <label class="col"><input type="radio" name="Level" value="5" v-model="defaultSettings.Level"><span>5</span></label>
          </div>
          </div>

        </div><!--row-->

        <div class="form-group">
          <input type="submit" name="" value="Submit">
        </div>
        <div id="AlertContent"></div>
      </form>

      <HomepageIframe
        :defaultSettings="defaultSettings"
      />
    </div>

  </div>
</template>

<script>
import $ from 'jquery'
import BaseOption from '@/components/BaseOption'
import HomepageShortCut from '@/components/HomepageShortCut'
import HomepageIframe from '@/components/HomepageIframe'

import quickStartList from '../digital/quickStartList.json'
import bankList from '../digital/bankList.json'
import tradeTypeList from '../digital/tradeTypeList.json'

export default {
  components: {
    BaseOption,
    HomepageShortCut,
    HomepageIframe
  },
  data () {
    return {
      defaultSettings: {
        BankName: '',
        TradeType: '',
        Store: '',
        Info: '',
        Amount: '',
        Level: ''
      },
      status: null,
      quickStartList: {
        title: 'Short Cut',
        require: false,
        digital: quickStartList
      },
      bankList: {
        title: 'Bank',
        require: true,
        digital: bankList
      },
      tradeTypeList: {
        title: 'Trade Type',
        require: false,
        digital: tradeTypeList
      }
    }
  },
  methods: {
    submitMethod (event) {
      $('#exampleModal').modal({
        show: true,
        keyboard: false,
        backdrop: 'static'
      })
      const forminfo = {
        form_sn: '1FAIpQLSeiYTmLmNOKOLfBFigK-0wc9gfL7ftY31ijTZfPDxQW_ZcgEA',
        key: 'entry.',
        bankname: '1205608201',
        tradetype: '2008076845',
        store: '329858999',
        amount: '232784372',
        info: '1013016713',
        level: '665941233'
      }
      let responseUri =
        'https://docs.google.com/forms/d/e/' +
        forminfo.form_sn + '/formResponse?' +
        forminfo.key + forminfo.bankname + '=' + this.defaultSettings.BankName + '&' +
        forminfo.key + forminfo.tradetype + '=' + this.defaultSettings.TradeType + '&' +
        forminfo.key + forminfo.store + '=' + this.defaultSettings.Store + '&' +
        forminfo.key + forminfo.amount + '=' + this.defaultSettings.Amount + '&' +
        forminfo.key + forminfo.info + '=' + this.defaultSettings.Info + '&' +
        forminfo.key + forminfo.level + '=' + this.defaultSettings.Level

      document.getElementById('actionFrame').src = responseUri
    }
  }
}
</script>

<style>
</style>



