import * as mutationTypes from './mutationTypes'
import * as stateTypes from '@/settings/stateTypes'

export default {
  [mutationTypes.SET_PAGINATOR] (state, { appType, paginator }) {
    state[`${appType}${stateTypes.PAGINATOR}`] = paginator
  },
  [mutationTypes.SET_ITEMS] (state, { appType, items }) {
    state[`${appType}${stateTypes.ITEMS}`] = items
  },
  [mutationTypes.SET_ITEM] (state, { appType, item }) {
    state[`${appType}${stateTypes.ITEM}`] = item
  }
}



