<template>
  <div class="container" >
    <div v-if="status === null">
      Loading...
    </div>
    <div class="rwd-table" v-if="status">
      <table>
        <tr class="title">
          <td>Type</td>
          <td colspan="3">Previous Mounth</td>
          <td colspan="3">This Month</td>
          <td colspan="1">Next Month</td>
        </tr>
        <tr
          v-for="(item, index) in datafilter()"
          :key="index"
        >
          <td>
            {{ item[0].type }}
          </td>
          <!-- Last -->
          <td>
            {{ item[1].prev }}
          </td>
          <td>
            {{ item[2].prev }}
          </td>
          <td class="total last">
            {{ Number(item[1].prev) + Number(item[2].prev) }}
          <!-- This -->
          <td>
            {{ item[1].this }}
          </td>
          <td>
            {{ item[2].this }}
          </td>
          <td class="total curr">
            {{ Number(item[1].this) + Number(item[2].this) }}
          </td>
          <!-- Next -->
          <td class="total next">
            {{ item[2].next }}
          </td>
        </tr>

        <tr class="total">
          <td>Total</td>
          <td></td><td></td><td>{{ analyticTotal.prev }}</td>
          <td></td><td></td><td>{{ analyticTotal.curr }}</td>
          <td>{{ analyticTotal.next }}</td>
        </tr>
      </table>
    </div>
  </div>
</template>

<script>
import axios from 'axios'
// import $ from 'jquery'

export default {
  name: 'HelloWorld',
  components: {
  },
  data () {
    return {
      status: null,
      items: {
        layaway: [],
        reciept: []
      },
      selectYear: null,
      analyticTotal: {
        prev: 0,
        curr: 0,
        next: 0
      }
    }
  },
  created: function () {
    const _this = this
    axios.all([
      axios.get('https://sheets.googleapis.com/v4/spreadsheets/1Axd3WeIglDpqvrlCcsj1vodsU4_JdLgrY1DqJ3XsIKA/values/DatabaseCurrent', {
        params: {
          key: 'AIzaSyCo4aab5RX5zOH5MOnRUtGHEL63ftpkknQ'
        }
      }),
      axios.get('https://sheets.googleapis.com/v4/spreadsheets/1Axd3WeIglDpqvrlCcsj1vodsU4_JdLgrY1DqJ3XsIKA/values/DatabaseLayaway', {
        params: {
          key: 'AIzaSyCo4aab5RX5zOH5MOnRUtGHEL63ftpkknQ'
        }
      }),
      axios.get('https://sheets.googleapis.com/v4/spreadsheets/1Axd3WeIglDpqvrlCcsj1vodsU4_JdLgrY1DqJ3XsIKA/values/Data!A3:C99', {
        params: {
          key: 'AIzaSyCo4aab5RX5zOH5MOnRUtGHEL63ftpkknQ'
        }
      })
    ])
      .then(
        axios.spread(
          function (reciept, layaway, bankdata) {
            _this.items.reciept = reciept.data.values
            _this.items.layaway = layaway.data.values
            _this.sort = bankdata.data.values
            _this.status = 'updated'
          }
        )
      )
  },
  methods: {
    datafilter: function () {
      const _this = this
      var sort = this.sort
      var result = []
      const items = this.items
      let lastTotal = {
        prev: 0,
        curr: 0,
        next: 0
      }

      sort.forEach((thisSort) => {
        var update = [{'type': thisSort[0]}] // bankname

        // starting handle reciepts
        function thisSortRecieptTotal () {
          // filter the bankname
          var ResultCurrent = items.reciept.filter(
            function (item, index, array) {
              return item[2].toLowerCase().toString() === thisSort[0].toLowerCase().toString()
            }
          )

          // sum th amount
          var currentAmount = 0
          var prevAmount = 0
          var nextAmount = 0
          ResultCurrent.forEach(
            element => {
              var thisDate = new Date(element[1])
              const d = new Date()
              var prevC = new Date(d.getFullYear(), d.getMonth() - 1, thisSort[1])
              var thisC = new Date(d.getFullYear(), d.getMonth(), thisSort[1])
              var nextC = new Date(d.getFullYear(), d.getMonth() + 1, thisSort[1])
              var nextCe = new Date(d.getFullYear(), d.getMonth() + 2, thisSort[1])
              if (thisDate >= prevC && thisDate < thisC && element[5]) {
                prevAmount += Number(element[5].replace(',', ''))
                lastTotal.prev += Number(element[5].replace(',', ''))
              }
              if (thisDate >= thisC && thisDate < nextC && element[5]) {
                currentAmount += Number(element[5].replace(',', ''))
                lastTotal.curr += Number(element[5].replace(',', ''))
              }
              if (thisDate >= nextC && thisDate < nextCe && element[5]) {
                nextAmount += Number(element[5].replace(',', ''))
                lastTotal.next += Number(element[5].replace(',', ''))
              }
            }
          )

          return {
            'type': 'reciept',
            'this': currentAmount,
            'prev': prevAmount,
            'next': nextAmount
          }
        }

        // starting hadle layaway
        function thisSortLayawayTotal () {
          // filter the bankname
          var ResultLayaway = items.layaway.filter(
            function (item, index, array) {
              return item[3].toLowerCase().toString() === thisSort[0].toLowerCase().toString()
            }
          )

          // sum th amount
          var currentAmount = 0
          var prevAmount = 0
          var nextAmount = 0
          ResultLayaway.forEach(
            element => {
              var thisDate = new Date(element[2])
              const d = new Date()
              var prevC = new Date(d.getFullYear(), d.getMonth() - 1, thisSort[1])
              var thisC = new Date(d.getFullYear(), d.getMonth(), thisSort[1])
              var nextC = new Date(d.getFullYear(), d.getMonth() + 1, thisSort[1])
              var nextCe = new Date(d.getFullYear(), d.getMonth() + 2, thisSort[1])
              if (thisDate >= prevC && thisDate < thisC && element[8]) {
                prevAmount += Number(element[8].replace(',', ''))
                lastTotal.prev += Number(element[8].replace(',', ''))
              }
              if (thisDate >= thisC && thisDate < nextC && element[8]) {
                currentAmount += Number(element[8].replace(',', ''))
                lastTotal.curr += Number(element[8].replace(',', ''))
              }
              if (thisDate >= nextC && thisDate < nextCe && element[8]) {
                nextAmount += Number(element[8].replace(',', ''))
                lastTotal.next += Number(element[8].replace(',', ''))
              }
            }
          )
          return {
            'type': 'layaway',
            'this': currentAmount,
            'prev': prevAmount,
            'next': nextAmount
          }
        }

        update.push(thisSortRecieptTotal())
        update.push(thisSortLayawayTotal())
        result.push(update)
      })

      // Update AnalyticTatl
      _this.analyticTotal.prev = lastTotal.prev
      _this.analyticTotal.curr = lastTotal.curr
      _this.analyticTotal.next = lastTotal.next
      return result
    }
  }
}
</script>

<style lang="scss">
@import '../assets/sass/mixins/index.scss';

.rwd-table{
  display: block;
  width: 100%;
  height: auto;
  overflow-y:auto;
  table{
    width: 100%;
    min-width: 600px;
    tr{
      &.title{
        text-align: center;
        background: default(color, dos);
      }
      &:not(.title):hover{
        background: fade-out(default(color, dos), .8);
      }
      td{
        padding: .3em .8em;
        font-size: .8em;
        border: 1px solid #fafafa !important;
        &.total{
          background: fade-out(default(color, uno), .6)
        }
      }
      &:not(.title){
        td{
          border-bottom: 1px solid default(color, dos) !important;
          letter-spacing: .08em;
          color: rgb(80, 80, 80);
          &:not(:first-child){
            text-align: right;
          }
        }
      }
    }
  }
}
</style>



