import axios from 'axios'
import LRU from 'lru-cache'

import * as settings from '@/settings'

export default {
  serverURL: settings.SERVER_URL,

  // use axios to handle ajax
  http: axios.create({
    baseURL: settings.API_BASE_URL
  }),

  cache: LRU({
    max: 1000,
    maxAge: 1000 * 60 * 10 // 10 min cache
  }),

  // get with cache
  get (url, useCache = true) {
    // console.log(`fetching ${url}, useCache=${useCache}:`)
    const api = this
    const cache = api.cache
    if (useCache && cache && cache.has(url)) {
      // console.log(`cache hit for ${url}.`)
      return Promise.resolve(cache.get(url))
    } else {
      return new Promise((resolve, reject) => {
        api.http.get(url)
          .then(response => {
            // console.log(`fetched ${url}.`)
            const data = response.data
            if (useCache) cache.set(url, data)
            resolve(data)
          })
          .catch(reject)
      })
    }
  },

  appEndpoints (appType) {
    return settings.ENDPOINTS[appType]
  }
}



