import Vue from 'vue'
import Router from 'vue-router'

import * as appTypes from '@/settings/appTypes'
import AppHome from '@/views/AppHome'
import AppAnalytic from '@/views/AppAnalytic'
import AppDetails from '@/views/AppDetails'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '*',
      redirect: '/' + appTypes.HOME
    },
    {
      path: '/' + appTypes['HOME'],
      component: AppHome
    },
    {
      path: '/' + appTypes['ANALYTIC'],
      component: AppAnalytic
    },
    {
      path: '/' + appTypes['DETAILS'],
      component: AppDetails
    }
  ]
})



