<template>
  <div
    class="google authorization"
    :class="{'logined': userData.status}"
  >
    <div
      id="gSignin2"
      class="g-signin2 login"
      ref="signinBtn"
    >
      <div class="block pointer">
        <div class="content">
          <div class="text">
            <h1 class="title">Sign in with <br>Google</h1>
            <p>
              Please sign in with your google account to access this page.
            </p>
            <p class="note">
              It's only use for vaild the account, we will not store any information from your account.
            </p>
          </div>
          <div class="signin btn"></div>
          <div class="google icon deco"></div>
        </div>
      </div>
    </div>

    <div class="logout">
      <div class="dropdown">
        <div
          class="dropdown-toggle headshot"
          id="dropdownMenuButton"
          data-toggle="dropdown"
          aria-haspopup="true"
          aria-expanded="false"
        >
          <img :src="userData.photo" alt="">
        </div>
        <div class="dropdown-menu" aria-labelledby="dropdownMenuButton">
          <div class="blocks inf-block">
            <div class="headshot">
              <div>
                <img :src="userData.photo">
              </div>
            </div>
            <div class="content">
              <span class="name">{{userData.name}}</span>
              <span class="email">{{userData.email}}</span>
            </div>
          </div>
          <div class="blocks so-block">
            <a
              href="#"
              @click="signOut()"
            >Sign out</a>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import $ from 'jquery'
import md5 from 'js-md5'

export default {
  props: ['userData'],
  mounted: function () {
    // const _self = this
    const gapi = window.gapi
    const updateUserData = this.updateUserData
    const vaildKey = [
      '55ea1d82a9445d0ecd0afa66c3c56d70',
      '95534dc81da1e1f80a673f8946bbefd9'
    ]
    // const userData = this.userData

    if (gapi) {
      var onSignIn = function (googleUser) {
        // update Status
        updateUserData('status', googleUser)

        if (googleUser === true) {
          var profile = gapi.auth2.getAuthInstance().currentUser.get().getBasicProfile()
          let id = profile.getId()
          let name = profile.getName()
          let photo = profile.getImageUrl()
          let email = profile.getEmail()
          updateUserData('id', id)
          updateUserData('name', name)
          updateUserData('photo', photo)
          updateUserData('email', email)
          let dKey = md5(id + name + email + 'FRsTallybook')
          console.log(dKey)
          // console.log(vaildKey.indexOf(dKey))
          if (vaildKey.indexOf(dKey) >= 0) {
            updateUserData('vaild', true)
          }
        } else {
          // console.log('Sign out')
          updateUserData('id', null)
          updateUserData('name', null)
          updateUserData('photo', null)
          updateUserData('email', null)
          updateUserData('status', null)
          updateUserData('vaild', false)
        }
      }
      gapi.load('auth2', function () {
        // console.log('hooked up auth2')
        gapi.auth2.init({
          client_id: '180203149475-ufhv2hdsa7ug3cqbsn0famgvsv1hqtlv.apps.googleusercontent.com'
        }).then(function (auth2) {
          // is logined
          onSignIn(auth2.isSignedIn.get())

          // listen
          auth2.isSignedIn.listen(onSignIn)
          $('#gSignin2').on('click', function () {
            auth2.signIn()
          })
        })
      })
    } else {
      location.reload()
    }
  },
  methods: {
    updateUserData: function (key, item) {
      this.userData[key] = item
    },
    signOut: function () {
      const userData = this.userData
      var auth2 = window.gapi.auth2.getAuthInstance()
      auth2.signOut().then(function () {
        userData.id = userData.name = userData.mail = userData.photo = null
        userData.status = false
        // location.reload()
      })
    }
  }
}
</script>

<style lang="scss">
@import '../assets/sass/mixins/index.scss';

.google.authorization{
  *{
    font-family: 'Lato', sans-serif !important;
  }
  .login{
    display: block;
  }
  .logout{
    display: none;
  }
  &.logined{
    .logout{display: block;}
    .login{display: none;}
  }

  .g-signin2{
    display: block;
    margin: 0 auto;
    width: 100%;
    max-width: 480px;
    height: auto;
    background: white;
    overflow: hidden;
    border-radius: .3em;
    box-shadow: 0 1px 4px rgba(0,0,0,.08);
    .block{
      position: relative;
      display: block;
      width: 100%;
      @include rps(u) {
        height: 0;
        padding-bottom: 75%;
      }
      @include rps(d) {
        height: auto;
      }
      &.pointer{
        *{
          cursor: pointer;
        }
      }
      .content{
        @include rps(u) {
          position: absolute;
        }
        display: block;
        width: 100%;
        height: 100%;
        left: 0;
        top: 0;
        padding: 2.5em;
        .text{
          .title{
            color: rgb(79, 139, 245);
          }
          h1{
            display: block;
            font-size: 2.5em;
            line-height: 1.2em;
            font-weight: bold;
            letter-spacing: 0;
          }
        }
        p{
          display: block;
          margin-bottom: .8em;
          line-height: 1.2em;
          &.note{
            font-size: .9em;
            color: rgba(150,150,150,1);
          }
        }
        .signin.btn{
          @include rps(u) {
            position: absolute;
            left: 2.5rem;
            bottom: 1.25em;
          }
          @include rps(d) {
            margin-top: 2.5em;
          }
          content: '';
          display: block;
          width: 100%;
          height: 2.5em;
          background-image: url('data:image/png;base64,');
          background-position: left center;
          background-size: contain;
          background-repeat: no-repeat;
        }
        .deco{
          content: '';
          display: block;
          width: 38%;
          height: 0;
          padding-bottom: 38%;
          position: absolute;
          top: -5%;
          right: -3%;
          @include opacity(.2);
        }
      }
    }
  }

  .logout{
    .dropdown{
    display: flex;
    width: 100%;
    justify-content: center;
    align-items: flex-end;
    flex-direction: column;
      .dropdown-toggle{
        display: block;
        width: 2.5em;
        height: 2.5em;
        overflow: hidden;
        border-radius: 5em;
        img{
          display: block;
          width: 100%;
          height: auto;;
        }
      }
      .dropdown-menu{
        width: 300px;
        border-radius: 2px;
        box-shadow: 0 3px 9px rgba(0,0,0,.08);
        padding: 0 !important;
        .blocks{
          &.inf-block{
            display: flex;
            flex-direction: row;
            .headshot{
              flex: 1 0 30%;
              display: block;
              width: 100%;
              height: auto;
              div{
                padding: 1em 1em 0 1em;
                display: block;
                overflow: hidden;
                width: 100%;
                height: 100%;
                img{
                  display: block;
                  width: 100%;
                  height: auto;
                  border-radius: 10em;
                }
              }
            }
            .content{
              flex: 1 0 50%;
              box-sizing: content-box;
              overflow: hidden;
              padding: 1em 1.25em .8em 0;
              span{
                display: block;
                width: 100%;
                height: auto;
                overflow: hidden;
                font-size: 1em;
                line-height: 1em;
                word-break: break-all;
                word-wrap: break-word;
                &.name{
                  font-size: .9em;
                  font-weight: bold;
                  padding-bottom: .3em;
                }
                &.email{
                  font-size: 13px;
                }
              }
            }
          }
          &.so-block{
            padding: .5em 1.25em;
            display: flex;
            flex-direction: center;
            align-items: center;
            justify-content: flex-end;
            width: 100%;
            margin-top: 1.25em;
            background: rgb(245, 245, 245);
            border-top: 1px solid rgb(198, 198, 198);
            a{
              letter-spacing: 0;
              line-height: 1em;
              font-size: .9em;
              font-weight: normal;
              display: inline-block;
              background: rgb(252, 252, 252);
              color: rgb(102, 102, 102);
              padding: .5em .9em;
              border: 1px solid rgb(198, 198, 198);
              border-radius: 3px;
            }
          }
        }
      }
    }
  }

  .google.icon{
    background-image: url('data:image/svg+xml;base64,');
    background-repeat: no-repeat;
    background-position: center;
    background-size: contain;
  }
}
</style>



